@media only screen and (min-width:600px) {

.next_card_button {
    display: inline-block;
    padding: 15px 25px;
    font-size: 2vw;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    outline: none;
    color: #000000;
    background-color: #ffffff;
    border: none;
    border-radius: 15px;
    box-shadow: 0 9px #999;
    margin-left:43%;
    z-index:1000;
    
}


.next_card_button:active {
    background-color: #979797;
    box-shadow: 0 5px #666;
    transform: translateY(4px);
}

.home_button {
    display: inline-block;
    padding: 15px 25px;
    font-size: 2vw;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    outline: none;
    color: #000000;
    background-color: #ffffff;
    border: none;
    border-radius: 15px;
    box-shadow: 0 9px #999;
    margin:1%;
}

.shearer_image {
    width:15%;
    position:absolute;
    rotate:-10deg;
    top:30%;
    right:-3%;
    overflow:hidden;
    filter: drop-shadow(0px 0px 15px #292929);
}

.speech_bubble {
    width:20%;
    position: absolute;
    top:5%;
    right:5%;
    rotate:10deg;
    filter: drop-shadow(0px 0px 15px #292929);
    
}
.shearer_text_name { 
    font-size:2.8vw;
    position: absolute;
    top:4%;
    right:9%;
    rotate:10deg;
    text-decoration:bold;

}

.shearer_text { 
    font-size:1.7vw;
    position: absolute;
    top:15%;
    right:9.5%;
    rotate:10deg;
    

}

.white_wheel_container {
    position:absolute;
    top:2%;
    right:56%;
    z-index:1;
    height:10%;
    width:10%;
}


}

/* --------------------------------------------------------------------------------------------------------------------------------------- */
/* The CSS Below is for mobile and smaller screen sizes */
@media only screen and (max-width:600px) {

    .home_container {
        text-align: center
    }
    
    .homepage_title {
        text-align: center;
        font-family: Arial, Helvetica, sans-serif;
        font-size: 4vw;
        padding: 10px;
        margin-bottom: 1px;
        color: rgb(255, 255, 255);
        filter: drop-shadow(0px 0px 15px #292929);
    }
    
    .next_card_button {
        display: inline-block;
        padding: 15px 25px;
        font-size: 5vw;
        cursor: pointer;
        text-align: center;
        text-decoration: none;
        outline: none;
        color: #000000;
        background-color: #ffffff;
        border: none;
        border-radius: 15px;
        box-shadow: 0 9px #999;
        margin-left:35%;
        position:absolute;
        top:1%;
    }
    
    
    .next_card_button:active {
        background-color: #979797;
        box-shadow: 0 5px #666;
        transform: translateY(4px);
    }
    
    .home_button {
        display: inline-block;
        padding: 15px 25px;
        font-size: 4vw;
        cursor: pointer;
        text-align: center;
        text-decoration: none;
        outline: none;
        color: #000000;
        background-color: #ffffff;
        border: none;
        border-radius: 15px;
        box-shadow: 0 9px #999;
        margin:2%;
    }

    .shearer_image {
        width:15%;
        position:absolute;
        rotate:-10deg;
        top:14%;
        right:-3%;
        overflow:hidden;
        filter: drop-shadow(0px 0px 15px #292929);
    }
    
    .speech_bubble {
        width:25%;
        position: absolute;
        top:5%;
        right:5%;
        rotate:10deg;
        filter: drop-shadow(0px 0px 15px #292929);
        
    }
    .shearer_text_name { 
        font-size:2.8vw;
        position: absolute;
        top:6%;
        right:9%;
        rotate:10deg;
        text-decoration:bold;
    
    }
    
    .shearer_text { 
        font-size:2vw;
        position: absolute;
        top:8%;
        right:8%;
        rotate:10deg;
    }
    .canvas {
        height:10%;
        width:10%;
    }

    .white_wheel_container {
        position:absolute;
        top:2%;
        right:56%;
        height:10%;
        width:10%;
    }

}